.product-card {
  box-sizing: border-box;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  padding: 32px 16px 16px;
  transition: transform .3s;
}

.product-card:hover {
  transform: scale(1.2);
}

.product-card__image {
  max-width: 160px;
  height: 134px;
  margin: 0 auto 40px;
  display: block;
}

.product-card__name {
  margin: 0 0 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
  transition: color .3s;
}

.product-card:hover .product-card__name {
  color: #34568b;
}

.product-card__code {
  color: #616070;
  margin: 0 0 16px;
  font-size: 10px;
  line-height: 14px;
}

.product-card__reviews {
  justify-content: space-between;
  align-items: flex-end;
  margin-bottom: 24px;
  display: flex;
}

.product-card__reviews-paragraph {
  margin: 0;
  font-size: 10px;
  line-height: 14px;
}

.product-card__price {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 16px;
  display: flex;
}

.product-card__price-label {
  color: #616070;
  margin: 0;
  font-size: 12px;
  line-height: 18px;
}

.product-card__price-value {
  margin: 0;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.product-card__buy {
  box-sizing: border-box;
  color: #fff;
  text-transform: uppercase;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  height: 40px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  transition: all .3s;
  display: flex;
}

.product-card__buy:hover {
  color: #00acdc;
  background-color: #fff;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.header__nav-list {
  text-transform: uppercase;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.header__nav-link {
  color: inherit;
  align-items: center;
  height: 60px;
  font-size: 12px;
  font-weight: 500;
  text-decoration: none;
  transition: color .3s;
  display: flex;
  position: relative;
}

.header__nav-link:hover {
  color: #00acdc;
}

.header__logo {
  height: 40px;
}

.header__nav-item:not(:first-child) {
  margin-left: 20px;
}

.is-active {
  color: #00acdc;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
  right: 0;
}

.product-catalog {
  grid-template-columns: repeat(1, 1fr);
  grid-auto-rows: 1fr;
  justify-content: center;
  justify-items: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .product-catalog {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) {
  .product-catalog {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  .product-catalog {
    grid-template-columns: repeat(4, 200px);
  }
}

:root {
  color: #060b35;
  font-family: Roboto, sans-serif;
}

.page__body {
  margin: 0;
}

.stars {
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars__star:last-child {
  margin-right: 0;
}

.stars--1 :first-child, .stars--2 :nth-child(-n+2), .stars--3 :nth-child(-n+3), .stars--4 :nth-child(-n+4), .stars--5 :nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}
